@import url(https://fonts.googleapis.com/css?family=Over+the+Rainbow);
@import url(https://fonts.googleapis.com/css?family=Orbitron);
@import url(https://fonts.googleapis.com/css?family=Indie+Flower);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

a {
  text-decoration: none !important; }

.top-menu {
  background-color: white !important;
  height: 50px !important;
  border-bottom: #21ba45; }

.top-menu button {
  background-color: #21ba45 !important;
  border: 1px solid #21ba45 !important; }

.top-menu button:hover {
  background-color: white !important;
  color: #21ba45 !important;
  border: 1px solid #21ba45; }

.top-menu .brand {
  font-family: 'Over the Rainbow', cursive;
  font-style: italic;
  font-size: 20px;
  color: #21ba45 !important; }

.ui.secondary.pointing.menu {
  border-bottom: 1px solid #21ba45; }

.spinner-grow {
  height: 10rem !important;
  width: 10rem !important;
  margin: 20vh 43vw;
  overflow: hidden;
  background-color: #21ba45; }

textarea {
  width: 100%;
  height: 100%; }

input {
  width: 100%;
  height: 100%; }

#article-holder {
  text-align: center; }

#article-holder p span {
  font-family: 'Open Sans', sans-serif !important;
  font-size: 40px; }

#article-holder h3 {
  color: grey; }

#article-holder img {
  max-width: 680px; }

h2.art-title {
  font-family: 'Orbitron', sans-serif;
  font-size: 34px; }

#single-article-container {
  max-width: 80%;
  font-size: 20px;
  margin: 0px auto;
  text-align: center;
  padding: 10px 100px; }

#edit-article-holder {
  width: 80%; }

.button-controls a {
  position: fixed;
  width: 45px;
  height: 45px;
  color: #FFF;
  border-radius: 50px;
  text-align: center;
  box-shadow: 2px 2px 3px #999; }

.float-e {
  bottom: 70px;
  right: 90px;
  background-color: #21ba45; }

.button-controls a i {
  margin-top: 16px; }

.float-t {
  bottom: 20px;
  right: 90px;
  background-color: red; }

#create-article-container {
  width: 80%;
  margin: auto; }

#create-article-container .public-DraftEditor-content[contenteditable=true] {
  height: 30vh; }

#articles-list-container {
  width: 55%;
  margin-left: 20vw;
  margin-top: 20px; }

.article-container {
  padding: 35px; }

.image {
  width: 80px !important;
  height: 80px !important; }

.authors-haven-header {
  padding-top: 200px;
  background-image: url(/static/media/bg.d2f21696.jpg);
  background-position: center;
  background-size: cover;
  text-align: center;
  font-size: 40px;
  height: 500px !important;
  font-family: 'Indie Flower', cursive; }

.article-title {
  font-family: 'Open Sans', sans-serif;
  color: black !important; }

.article-description {
  color: grey !important;
  letter-spacing: 1.0px;
  font-size: 16px; }

.article-tags {
  background-color: #cccccc !important; }

#pagination {
  margin-left: 280px;
  margin-bottom: 20px; }

.cover-image {
  width: 100%;
  height: 60vh;
  background-image: url("https://picsum.photos/1200/800/?random"); }

#avgRating {
  font-size: 2em;
  color: goldenrod;
  padding-left: 5px; }

.avrgRating {
  font-family: 'Orbitron', sans-serif;
  margin-bottom: 5em; }

.home {
  padding-top: 5%; }

#pull-right {
  padding-left: 31%; }

#personal-info {
  padding-left: 28%; }

#username {
  text-align: center;
  padding-top: 1%; }

.padding {
  padding: 20px !important;
  background: yellow; }

#the-btn {
  padding-left: 10%;
  top: 500%; }

#new {
  padding-top: 15%; }

#form {
  padding-top: 10%;
  width: 60%;
  padding-left: 30%; }

#profile-div {
  margin: auto;
  width: 70%; }

#table-div {
  width: 50%;
  margin: auto; }

.text-center {
  text-align: center; }

.modal {
  width: 50% !important;
  opacity: 1 !important;
  margin-left: 25% !important;
  height: auto; }
  .modal i {
    color: #000 !important;
    position: static !important;
    position: initial !important;
    margin: 1.5rem !important;
    padding: 0 !important; }

#followers {
  width: 20% !important;
  margin-left: 30vw; }

a {
  color: black; }

#comment {
  background-color: #21ba45 !important;
  border: 1px solid #21ba45 !important;
  margin-left: 474px; }

#comment :hover {
  background-color: white !important;
  color: #21ba45 !important;
  border: 1px solid #21ba45 !important; }

#comment-field {
  height: 40px;
  overflow: hidden; }

#comment-field:focus {
  height: 150px;
  overflow: hidden; }

#cancel-comment-button, #delete-comment-button, #update-popup-button {
  background-color: #21ba45 !important;
  border: 1px solid #21ba45 !important; }

#cancel-comment-button:hover, #delete-comment-button:hover, #update-popup-button:hover {
  background-color: white !important;
  color: #21ba45 !important;
  border: 1px solid #21ba45 !important; }

#avatar {
  margin-left: 200px; }

#author, #date {
  margin-right: 200px; }

.comment-body {
  margin-left: 200px !important;
  margin-right: 200px !important;
  margin-top: 8px !important; }

#reply-text {
  color: #000 !important;
  font-size: 12px !important; }

#edit {
  background-color: #21ba45;
  color: white;
  border: 1px solid #21ba45; }

#edit:hover {
  background-color: white;
  color: #21ba45;
  border: 1px solid #21ba45; }

#card {
  margin-bottom: 18px; }

.signup-div {
  margin: auto;
  color: green;
  margin-top: 7px; }

#submit {
  margin-top: 4px;
  background-color: #21ba45;
  color: white;
  border: 1px solid #21ba45; }

#submit:hover {
  margin-top: 4px;
  background-color: white;
  color: #21ba45;
  border: 1px solid #21ba45; }

#verify-mail-notif {
  font-size: 20px;
  text-align: center;
  margin-top: 20px; }

.reset-password {
  width: 50% !important;
  margin: 8% auto !important;
  text-align: center; }

